import React from 'react';
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import { FormTitleDiv, SelectBar, CheckBar } from '../../components'
import { Fonts, SelectStyles } from '../../themes'
import Select from 'react-select'

const ContentArea = styled.div`
    position: relative;
    width: 100%;
    box-sizing: border-box;
    display: grid;
    justify-items: center;
    .selectBar{
        max-width: 344px;
        margin: 0 auto;
    }
    .checkBar{
        margin: 0 auto;
        max-width: 250px;
        padding-bottom: 30px;
    }
`
const TextArea = styled.div`
    padding: 22px 24px 33px;
`

const Text = styled(Fonts.P_16())`
    text-align: center;
    .br{
        margin-bottom: 20px;
    }
`

const Refund = (props) => {
    const [ check, setCheck ] = React.useState(false);
    const reasons = [
        { value: "0", label: "臨時有事" },
        { value: "1", label: "原時間已有安排" },
        { value: "2", label: "改買其他票種" },
        { value: "3", label: "改變心意不想參加" },
        { value: "4", label: "身體有狀況無法參加" },
        { value: "5", label: "其他原因" },
    ];
    return (
        <FormTitleDiv title='辦理退票' close={props.close} Submit={props.submit}>
            <ContentArea>
                <SelectBar className='full' inputTitle='原因'>
                    <Select 
                        options={reasons} 
                        name='reasons'
                        isSearchable={ false }
                        placeholder='請選擇'
                        styles={SelectStyles}
                    />
                </SelectBar>
                <TextArea>
                    <Text>我們將會按彩虹數字協會的退票處理辦法進行退票作業</Text>
                    <Text className='br'>有任何問題請洽 <Link to='mailto:service@rainbow.com' target='_blank'>service@rainbow.com</Link></Text>
                    <Text >點擊確認後，票券即無法恢復。</Text>
                    <Text>有部分特殊情形，僅接受整筆訂單申請退票，無法按票券個別申請退票例如:雙人套票</Text>
                </TextArea>
                <CheckBar className='black'>
                    <input type="checkbox" name="time" id='time' onChange={()=>setCheck(!check)} />
                    <span htmlFor="time" className='bold'>我已閱讀並同意 退票處理辦法</span>
                </CheckBar>
            </ContentArea>
        </FormTitleDiv>
    );
}

export default Refund;