import React from 'react';
import styled from 'styled-components'
import { Fonts, Colors, Styles } from '../../themes'
import Sticky from 'react-stickynode';

const Wrapper = styled.div`
    background: white;
    border-radius: 16px;
    box-shadow: 0px 4px 8px 0 ${Colors.B10};
    width: 350px; 
    max-height: 540px;
    transition: .4s;
    top: 116px !important;
    &.forBottom{
        align-self: end;
    }
`
const Img = styled.img`
    width: 100%;
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
`
const TextArea = styled.div`
    padding: 16px 24px 16px;
`
const Title = styled(Fonts.h4())`
`
const Text = styled(Fonts.P_16())`
    padding: 4px 0;
    &.info{
        color: ${Colors.G2}
    }
`
const Grid = styled.div`
    display: grid;
    grid-template-columns: 1fr 45px;
    padding: 10px 0;
    border-bottom: 1px solid ${Colors.B10};
`
const Price = styled(Fonts.h4())`
    color:${Colors.Blue};
`
const Required = styled.p`
    background: ${Colors.Light_Red};
    padding: 2px 4px;
    border-radius: 4px;
    color: ${Colors.Red};
    text-align: center;
    align-self: end;
    margin: 0;
`
const InfoArea = styled.div`
    padding: 12px 0;
`
const ProgressArea = styled.div``
const ProgressText = styled(Fonts.C_12())``
const Progress = styled.div``
const Button = styled(Styles.btnL())`
`

const CardSignUpDetail = (props) => {
    const [bottom,setBottom] = React.useState(false)
    const height = props.height+100
    React.useEffect(() => {
        if(window.pageYOffset>0){
            setBottom(true)
            console.log(height)
        }else{
            setBottom(false)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])
    return (
        <Sticky enabled={true} top={116} bottomBoundary={height} activeClass='sticky' releasedClass='noSticky'>
            <Wrapper className={`${props.className} ${bottom ? 'forBottom' : '' }`} >
                <Img src={props.img} alt='' className='img'/>
                <TextArea>
                    <Title>{props.title}</Title>
                    <Text>{props.text}</Text>
                    <Grid>
                        <Price>NT$ {props.price}</Price>
                        {props.required === '必修' &&
                            <div><Required>{props.required}</Required></div>
                        }
                    </Grid>
                    <InfoArea>
                        <Text className='info'>課程總長：{props.duration}小時</Text>
                        <Text className='info'>課程地點：{props.location}</Text>
                        <Text className='info'>上課時間：{props.startTime}</Text>
                        <Text className='info'>上課教室：{props.classroom}</Text>
                    </InfoArea>
                    <ProgressArea>
                        <ProgressText> {props.now} / {props.all} {props.progressText}</ProgressText>
                        <Progress />
                    </ProgressArea>
                    <Button className='solid full-width disable'>立即報名</Button>
                </TextArea>
            </Wrapper>
        </Sticky>
    );
}

export default CardSignUpDetail;