import React from 'react';
import styled from 'styled-components'
import { Fonts, Colors, Styles } from '../../themes'
import Sticky from 'react-stickynode';

const Wrapper = styled.div`
    background: white;
    border-radius: 16px;
    box-shadow: 0px 4px 8px 0 ${Colors.B10};
    width: 350px;
    max-height: 220px;
    transition: .4s;
    top: 116px !important;
    ${'' /* &:hover {
        box-shadow: 0px 8px 12px 0 ${Colors.B20};
        transition: 0.8s cubic-bezier(0.2,0.8,0.2,1);
    } */}
    &.hasImg{
        max-height: 360px;
    }
    &.hasImg img{
        max-height: 500px;
        padding-bottom: 20px;
    }
    &.forBottom{
        align-self: end;
    }
`
const Img = styled.img`
    width: 100%;
    max-height: 0;
    border-radius: 16px;
`
const TextArea = styled.div`
    padding: 0px 24px 16px;
`

const Title = styled(Fonts.h4())`
 
`
const Grid = styled.div`
    display: grid;
    grid-template-columns: 1fr 45px;
    padding: 10px 0;
`
const Price = styled(Fonts.h4())`
    color:${Colors.Blue};
`
const Required = styled.p`
    background: ${Colors.Light_Red};
    padding: 2px 4px;
    border-radius: 4px;
    color: ${Colors.Red};
    text-align: center;
    align-self: end;
    margin: 0;
`

const Button = styled(Styles.btnL())`
`

const CardEventSignUp = (props) => {
    const [bottom,setBottom] = React.useState(false)
    const height = props.height+100
    React.useEffect(() => {
        if(window.pageYOffset>0){
            setBottom(true)
            console.log(height)
        }else{
            setBottom(false)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])
    return (
        <Sticky enabled={true} top={116} bottomBoundary={height} activeClass='sticky' releasedClass='noSticky'>
                <Wrapper className={`${props.className} ${bottom ? 'forBottom' : '' }`} >
                    <Img src={props.img} alt='' className='img'/>
                    <TextArea>
                        <Title>{props.title}</Title>
                        <Grid>
                            <Price>{props.price}</Price>
                            {props.required === '必修' &&
                                <div><Required>{props.required}</Required></div>
                            }
                        </Grid>
                        <Button className='solid full-width disable'>立即報名</Button>
                    </TextArea>
                </Wrapper>
        </Sticky>
    );
}

export default CardEventSignUp;