import React from 'react'
import styled from 'styled-components'
import { Colors, Metrics } from '../../themes'
import 'antd/dist/antd.css';

const InputArea = styled.div`
    width: 100%;
    margin: 8px 0;
    padding: 8px 16px 4px;
    position: relative;
    z-index: 1000000;
    display: grid;
    max-width: 344px;
    .ant-picker,.ant-space,input[type=date],input[type=time]{
        width: 100%;
        max-width: 344px;
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        z-index: 0;
        background: ${Colors.W10};
        color: ${Colors.W100};
    }
    &.black .ant-picker,.ant-space,input[type=date],input[type=time]{
        color: ${Colors.B100};
    }
    input[type=date],input[type=time]{
        min-height: 40px;
    }
    .ant-picker,input[type=date],input[type=time]{
        border: 1px solid ${Colors.W80};
        border-radius: 6px;
        padding: 8px 16px;
        transition: .2s;
        svg{
            fill: ${Colors.W100};
        }
    }
    &.black .ant-picker,input[type=date],input[type=time]{
        border: 1px solid ${Colors.B10};
        svg{
            fill: ${Colors.B30};
        }
    }
    &.black .ant-picker:hover,input[type=date],input[type=time]{
        border: 1px solid ${Colors.Blue};
        svg{
            fill: ${Colors.Blue};
        }
    }
    .ant-picker-focused{
        border: 1px solid ${Colors.W100};
        box-shadow: inherit !important;
        width: 100%;
        svg{
            fill: ${Colors.W100};
        }
    }
    &.black .ant-picker-focused{
        border: 1px solid ${Colors.Blue};
        svg{
            fill: ${Colors.Blue};
        }
    }
    input[type=time]:focus,input[type=date]:focus{
        border: 1px solid ${Colors.W100};
        box-shadow: inherit !important;
        width: 100%;
        outline: none;
    }
    &.black input[type=time]:focus,input[type=date]:focus{
        border: 1px solid ${Colors.Blue};
    }
    img {
        position: absolute;
        left: 85%;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
        pointer-events: none;
    }
    input[type=time]:focus + img,input[type=date]:focus + img{
        filter: invert(0.4) sepia(1) saturate(20) hue-rotate(187.2deg) brightness(0.84);
    }
    input[type="date"]:before, input[type="time"]:before{ 
    	content: attr(placeholder);
    	color: ${Colors.W100};
    }
    &.black input[type="date"]:before, input[type="time"]:before{ 
        color: ${Colors.G2};
    }
    input[type="date"]:focus:before,input[type="time"]:focus:before,
    .no-placeholder:valid:before { 
        display: none 
    }
    input[type="date"]::-webkit-calendar-picker-indicator,
    input[type="time"]::-webkit-calendar-picker-indicator{
        opacity: 0
    }
    .disable{
        background: ${Colors.W80};
        pointer-events: none;
    }
    &.black .disable{
        background: ${Colors.B30};
    }
    .alert{
        border: 1px solid ${Colors.Red};
    }
    .ant-picker-input > input{
        font-size: 16px;
        letter-spacing: 0;
        line-height: 22px;
        color: ${Colors.W100};
    }
    &.black .ant-picker-input > input{
        color: ${Colors.B100};
    }
    &.black p{
        color: ${Colors.B100};
    }
    &.disable{
        pointer-events: none;
    }
`
const InputTitle = styled.p`
    font-size: 12px;
    letter-spacing: 0;
    line-height: 17px;
    position: relative;
    width: 100%;
    padding-left: 8px;
    color: ${Colors.W100};
    @media (max-width: ${Metrics.mobile}){
        padding-left: inherit;
    }
`
const Input = styled.div`
    position: relative
`


const InputDateBar = ( props ) => {
    const { children } = props
    return ( 
        <InputArea className={props.className}>
            <InputTitle>{props.inputTitle}</InputTitle>
            <Input>
            { children }
            </Input>
        </InputArea>
    )
  }
  
  export default InputDateBar