import React from 'react'
import styled from 'styled-components'
import { Header, Footer, Copyright, LoginModal, Calculator, } from '../components'
import { Images, Colors, Metrics, } from '../themes'
import Modal from 'react-modal'
import MessengerCustomerChat from 'react-messenger-customer-chat';

const Close = styled.div`
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 10000;
    width: 24px;
    height: 24px;
    filter: invert(1);
    &:hover{
        cursor: pointer;
    }
`
const customStyles = {
    content : {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        borderRadius: '16px',
        boxShadow: '0 2px 4px 0 rgba(0,0,0,0.1)',
        zIndex: '1000',
    }
};

const Wrapper = styled.div`
    background: ${Colors.BackGround};
    overflow: scroll;
    position: relative;
    -ms-overflow-style: none;
    scrollbar-width: none;
    ::-webkit-scrollbar {
        display: none;
    }
`
const Content = styled.div`
    padding: 116px 0 5vw;
    position: relative;
    @media (max-width: 700px){
        padding-top: 85px;
    }
    @media (max-width: ${Metrics.mobile}){
        padding-top: 55px;
    }
`

const Layout = (props) => {
    const { children } = props
    const [ modalLogin, setLogin ] = React.useState(false);
    function openLogin() {
        setLogin(true);
    }
    function closeLogin(){
        setLogin(false);
    }

    const [modalIsOpen,setIsOpen] = React.useState(false);
    function openModal() {
        setIsOpen(true);
    }
    function closeModal(){
        setIsOpen(false);
    }
    return ( 
        <Wrapper ref={props.ref}>
            <Header 
                header={props.header} 
                event={props.event} 
                reservation={props.reservation} 
                team={props.team}
                learn={props.learn}
                onClick={openLogin}
            />
            <Content >
                { children }
            </Content>
            <Footer 
                event={props.event} 
                reservation={props.reservation} 
                team={props.team}
                learn={props.learn}
                onClick={openModal}
            />
            <Copyright />
            <Modal
                isOpen={modalLogin}
                onRequestClose={closeLogin}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Close onClick={closeLogin}>
                    <img src={Images.close} alt='' />
                </Close>
                <LoginModal />
            </Modal>
            <Modal
                isOpen={modalIsOpen}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Close onClick={closeModal}>
                    <img src={Images.close} alt='' />
                </Close>
                <Calculator />
            </Modal>
            <MessengerCustomerChat
                pageId="114171120372277"
                appID="1477417075791752"
                theme_color="#0084ff"
                logged_in_greeting="需要幫忙嗎？歡迎留言給我們！"
                logged_out_greeting="需要幫忙嗎？歡迎留言給我們！"
            />
        </Wrapper>
    )
  }
  
  export default Layout