import React from 'react';
import { Seo, Layout, TitleArea, TitleOnlyArea, CardEvent, FlexFree } from '../components'
import styled from 'styled-components'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { Query } from "react-apollo";
import { gql } from "apollo-boost";
import { useHistory } from "react-router-dom";

const TabView = styled.div`
    padding: 24px 16px 40px;
`

const CardBottom = styled.div`
    height: 16px;
`

const EventPage = (props) => {
    const id = props.match?.params?.id;
    const [value, setValue] = React.useState(Number(id));
    const history = useHistory();
    const handleChange = (event, newValue) => {
        setValue(newValue);
        history.push(`${newValue}`)
    };
    let Get_List = gql`
        query MyQuery{
            event(order_by: {code: asc}){
                id
                title
                excerpt
                required
                type
                category
                cover
                
                sessions(limit: 1, order_by: {price: desc}) {
                    price
                }
            }
        }
    `;
    
    return (
        <Query query={Get_List}>
            {({ loading, error, data, refetch }) => {
            if (loading) return <p></p>;
            if (error) return <p>error</p>;
            const DefaultList = data.event.filter(i=>i.category==='核心基礎課程')
            const DeepList = data.event.filter(i=>i.category==='深度解碼課程')
            const UnitList = data.event.filter(i=>i.category==='單元主題課程' || i.category==='案例研析課程' )
        return (
            <Layout event>
                <Seo title='-彩虹數字學課程'/>
                <TitleArea
                    title='彩虹數字學課程'
                    text='彩虹數字學三大類型課程:「心性解碼」、「時間解碼」、「感情解碼」'
                />
                <Tabs
                    value={value}
                    className='EventTab'
                    onChange={handleChange}
                >
                    <Tab label='核心基礎' />
                    <Tab label='深度解碼' />
                    <Tab label='單元主題' />
                </Tabs>
                {value === 0 &&
                    <TabView>
                        <TitleOnlyArea title='心性解碼' />
                        <FlexFree>
                            {DefaultList.filter(i=>i.type==='心性解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <CardBottom />
                        <TitleOnlyArea title='時間解碼' />
                        <FlexFree>
                            {DefaultList.filter(i=>i.type==='時間解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <CardBottom />
                        <TitleOnlyArea title='感情解碼' />
                        <FlexFree>
                            {DefaultList.filter(i=>i.type==='感情解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <CardBottom />
                    </TabView>
                }
                {value === 1 &&
                    <TabView>
                        <TitleOnlyArea title='心性解碼' />
                        <FlexFree>
                            {DeepList.filter(i=>i.type==='心性解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <TitleOnlyArea title='時間解碼' />
                        <FlexFree>
                            {DeepList.filter(i=>i.type==='時間解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <TitleOnlyArea title='感情解碼' />
                        <FlexFree>
                            {DeepList.filter(i=>i.type==='感情解碼').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                    </TabView>
                }
                {value === 2 &&
                    <TabView>
                        <TitleOnlyArea title='單元主題' />
                        <FlexFree>
                            {UnitList.filter(i=>i.category==='單元主題課程').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                        <TitleOnlyArea title='案例分析' />
                        <FlexFree>
                            {UnitList.filter(i=>i.category==='案例研析課程').map((i, index) => (
                                <CardEvent
                                    onClick={()=>history.push(`/eventDetail/${i.id}`)}
                                    img={require(`../img/Card/${i.cover}.svg`)}
                                    title={i.title}
                                    text={i.excerpt}
                                    price={`NT$ ${i.sessions[0].price}`}
                                    required={i.required}
                                />
                            ))}
                        </FlexFree>
                    </TabView>
                }
            </Layout>
            );
        }}
    </Query>
    );
}

export default EventPage;