import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { Colors, Fonts, Images, Metrics } from '../themes'

const Wrapper = styled.div`
    width: 100%;
    position: fixed;
    z-index: 101;
    height: 100px;
    background: ${Colors.BackGround};
    transition: .3s;
    padding: 0 20px;
    &.HeaderScrolled{
        background: ${Colors.W100};
        box-shadow: 0 2px 4px 0 ${Colors.B10};
    }
    @media (max-width: 700px){
       height: 75px;
       transition: .4s;
       &.open{
         background: ${Colors.W100};
       }
       &.open.HeaderScrolled{
         box-shadow: inherit;
       }
    }
    @media (max-width: ${Metrics.mobile}){
        height: 55px;
        padding: 8px 20px;
    }
`

const Content = styled.div`
    display: flex;
    max-width: 1080px;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
`

const Logo = styled.img`
    height: 100px;
    width: auto;
    @media (max-width: 700px){
       height: 75px;
    }
    @media (max-width: ${Metrics.mobile}){
        height: 44px;
    }
`

const LinkAreaFull = styled.div`
    @media (max-width: 700px){
        position: fixed;
        top: 75px;
        max-height: 0;
        overflow: hidden;
        transition: .4s;
        z-index: 1000;
        background: ${Colors.W100};
        height: 0;
        width: 100%;
        &.open{
            height: 100%;
            max-height: 4000px;
            transition: .4s;
        }
    }
    @media (max-width: ${Metrics.mobile}){
        top: 44px;
        left: 0px;
    }
`

const LinkArea = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    a{
        margin: 0 16px;
    }
    .active h6{
        color: ${Colors.B100};
        font-weight: 700;
    }
    @media (max-width: 700px){
        display: grid;
        padding: 16px 14px;
    }
`

const Text = styled(Fonts.h6())`
    color: ${Colors.G3};
    &:hover {
        color: ${Colors.Blue};
        transition: 0.4s cubic-bezier(0.2,0.8,0.2,1);
    }
    @media (max-width: 700px){
        border-bottom: 1px solid ${Colors.B10};
        padding: 12px 0px;
        font-size: 16px;
    }
`

