import React from 'react';
import { Seo, Layout, CardBannerS, CardEventSignUp, CardEventDetail,CardEventSignUpFixed } from '../components'
import styled from 'styled-components'
import { Query } from "react-apollo";
import { gql } from "apollo-boost";
var moment = require('moment');

const Wrapper = styled.div`
    position: relative;
    height: auto;
    padding: 0 16px;
`
const Content = styled.div`
    padding: 24px 0;
    display: grid;
    grid-template-columns: auto 349px;
    max-width: 1080px;
    margin: 0 auto;
    grid-gap: 16px;
    @media (max-width: 700px){
        padding: 13px 0;
        grid-template-columns: 100%;
        grid-gap: inherit;
    }
`

const EventDetailPage = (props) => {
    const id = props.match?.params?.id;
    const Get_Detail = gql`
        query MyQuery {
            event(where: {id: {_eq: ${id} }}) {
                id
                title
                excerpt
                content
                required
                type
                category
                cover
                code
                sessions {
                    session_start_time
                    session_end_time
                    title
                    price
                    duration
                    teacher_id
                    member {
                        name
                    }
                    classroom {
                        city
                    }
                }
            }
        }
    `;
    const [hasScrolled, setScrolled] = React.useState(false)
    const [height, setHeight] = React.useState(0);
    const elementRef = React.createRef();
    const [width, setWidth] = React.useState(window.innerWidth);

    const updateWidthAndHeight = () => {
        setWidth(window.innerWidth);
    };
    React.useEffect(() => {
        // setHeight(elementRef.current.offsetHeight);

        if(elementRef.current){
            setHeight(elementRef.current.offsetHeight);
        }
        const headerScrollEvent = window.addEventListener('scroll', e =>
            setScrolled(window.pageYOffset > 320 && window.innerWidth > 425)
        )
        return () => {
            headerScrollEvent && window.removeEventListener(headerScrollEvent)
        }

       // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [elementRef])
     
    window.addEventListener("resize", updateWidthAndHeight);
    
    return (
        <Query query={Get_Detail}>
            {({ loading, error, data, refetch }) => {
            if (loading) return <p>Loading...</p>;
            if (error) return <p>Error :(</p>;
                const detail = data.event[0]
                const start = moment(detail.sessions[0].session_start_time).format("YYYY-MM-DD HH:mm:ss");
                const end = moment(detail.sessions[0].session_end_time).format("YYYY-MM-DD HH:mm:ss")
                const duration = moment(end, "YYYY-MM-DD HH:mm:ss").diff(moment(start, "YYYY-MM-DD HH:mm:ss"), 'hours') 
                const img = require(`../img/Card/${detail.cover}.svg`)
            return (
                
                    <Layout event >
                        <Seo title=''/>
                        <Wrapper ref={elementRef}>
                            <CardBannerS
                                className='center sh-img'
                                bg={img}
                                title={`${detail.title}`}
                                text={`${detail.excerpt}`}
                            />
                            <Content>
                                <CardEventDetail
                                    bigTitle={`${detail.title}`}
                                    excerpt={`${detail.excerpt}`}
                                    content={`${detail.content}`}
                                    teacher={`${detail.sessions[0].member.name}`}
                                    duration={`${duration}`}
                                    city={`${detail.sessions[0].classroom.city}`}
                                />
                                {width > 700 
                                    ? <CardEventSignUp
                                        title={`${detail.title} ${detail.code}`}
                                        price={`NT$ ${detail.sessions[0].price}`}
                                        required={`${detail.required}`}
                                        img={img}
                                        className={ hasScrolled ? 'hasImg' : '' }
                                        height={height}
                                    />
                                    : <CardEventSignUpFixed 
                                        title={`${detail.title} ${detail.code}`}
                                        price={`NT$ ${detail.sessions[0].price}`}
                                        required={`${detail.required}`}
                                        img={img}
                                        className={ hasScrolled ? 'hasImg' : '' }
                                        height={height}
                                    />
                                }
                                
                            </Content>
                        </Wrapper>
                        
                    </Layout>
              
                );
            }}
        </Query>
    );
}

export default EventDetailPage;